export const TdOptions = itemCreator(1, 41);

function itemCreator(from: number, to: number) {
  // if (to && from) {
  const rangeCount = to - from + 1;
  return Array(rangeCount)
    .fill('')
    .map((e, i) => {
      const order = from + i > 9 ? `${from + i}` : `0${from + i}`;
      return { label: order, value: order };
    });
}
