import React from 'react';
import ViewList from '@material-ui/icons/ViewList';
import { makeStyles } from '@material-ui/core/styles';
import { Grid, Checkbox } from '@material-ui/core';

import { SectionWrapper } from '../../../ui/SectionWrapper';
import { MultiTextField } from '../../../ui/TextField';
import { SelectField } from '../../../ui/SelectField';
import { venderOptions, materialOptions, repairReasonOptions } from '../../../data/comm';
import { TableForm } from '../../../ui/TableForm';
import { RadioGroupField } from '../../../ui/RadioGroupField';
import { PopGraphicField } from '../../popup/PopGraphicField';
import { DateField } from '../../../ui/DateField';

const useStyles = makeStyles(
  theme => ({
    row: {
      marginBottom: theme.spacing(2),
      borderBottom: `2px solid ${theme.palette.primary.main}`,
    },
    flatBtn: {
      color: 'white',
      boxShadow: theme.shadows[0],
      borderRadius: 4,
      fontSize: theme.typography.h4.fontSize,
      padding: '6px 12px',
    },
    fieldSamll: {
      '& .MuiInputBase-root': {
        width: 110,
      },
    },
    colorBorderField: {
      '& .MuiOutlinedInput-notchedOutline': {
        border: `3px solid ${theme.palette.primary.main}`,
        marginRight: -3,
      },
      '& .MuiInputBase-root': {
        height: 60,
      },
      '& .MuiInputBase-input::placeholder': {
        color: theme.palette.primary.main,
        opacity: 1,
      },
    },
  }),
  { name: 'MaintForm' },
);

export const MaintForm = () => {
  const classes = useStyles();

  const venderFormField = [
    {
      label: '',
      control: () => <SelectField defaultText="" options={venderOptions} />,
    },
    {
      label: '',
      control: () => <SelectField defaultText="" options={venderOptions} />,
    },
    {
      label: '',
      control: () => <SelectField defaultText="" options={venderOptions} />,
    },
  ];
  const eventHandleFormField = [
    {
      label: '',
      control: () => <MultiTextField fields={[{ placeholder: 'NN' }]} style={{ width: 102, padding: 4 }} />,
    },
    {
      label: '',
      control: () => <PopGraphicField title="" />,
    },
    {
      label: '',
      control: () => <PopGraphicField title="" />,
    },
    {
      label: '',
      control: () => <PopGraphicField title="" />,
    },
    {
      label: '',
      control: () => <PopGraphicField title="" />,
    },
    {
      label: '',
      control: () => <Checkbox size="medium" color="primary" />,
    },
    {
      label: '',
      control: () => <PopGraphicField title="" />,
    },
    {
      label: '',
      control: () => <PopGraphicField title="" />,
    },
  ];

  return (
    <SectionWrapper icon={<ViewList color="primary" fontSize="large" />} title="">
      <Grid container justify="space-between" alignItems="center" spacing={3} className={classes.row}>
        {/* 1st row */}
        <Grid item container xs={12} wrap="nowrap" justify="space-between" alignItems="center" spacing={3}>
          <Grid item xs={4}>
            <MultiTextField
              label=""
              // state={state}
              // onChange={()=>()}
              fields={[{ placeholder: 'YYYY-MM-NNN' }]}
              vertical
              fullWidth
            />
          </Grid>
          <Grid item xs={4}>
            <DateField label="" placeholder="YYYY-MM-DD" withDayTime />
          </Grid>
          <Grid item xs={4}>
            <MultiTextField
              label=""
              // state={state}
              // onChange={()=>()}
              fields={[{ placeholder: 'NN' }]}
              vertical
              fullWidth
            />
          </Grid>
        </Grid>
        {/* 2nd row */}
        <Grid item container xs={12} wrap="nowrap" justify="space-between" alignItems="center" spacing={3}>
          <Grid item xs={4}>
            <DateField label="S/N " placeholder="YYYY-MM-DD" withDayTime />
          </Grid>
          <Grid item xs={4}>
            <DateField
              label=""
              placeholder="YYYY-MM-DD"
              withDayTime
            />
          </Grid>
          <Grid item xs={4}>
            <MultiTextField
              label=""
              // state={state}
              // onChange={()=>()}
              fields={[{ placeholder: 'NN', suffix: '' }]}
              vertical
              fullWidth
            />
          </Grid>
        </Grid>
        {/* 3rd row */}
        <Grid item container xs={12} wrap="nowrap" justify="space-between" alignItems="center" spacing={3}>
          <TableForm label="" fields={venderFormField} vertical />
        </Grid>
        {/* 4th row */}
        <Grid item container xs={12} wrap="nowrap" justify="space-between" alignItems="center" spacing={3}>
          <RadioGroupField label="" options={materialOptions} vertical />
          <RadioGroupField label="" options={repairReasonOptions} vertical />
        </Grid>
        {/* 5th row */}
        <Grid item container xs={12} wrap="nowrap" justify="space-between" alignItems="center" spacing={3}>
          <TableForm label="" fields={eventHandleFormField} vertical />
        </Grid>
      </Grid>
    </SectionWrapper>
  );
};
