import React from 'react';
import SettingsApplications from '@material-ui/icons/SettingsApplications';
import { makeStyles } from '@material-ui/core/styles';
import { Grid } from '@material-ui/core';

import { SectionWrapper } from '../../../ui/SectionWrapper';
import { MultiTextField } from '../../../ui/TextField';
import { SelectField } from '../../../ui/SelectField';
import { venderOptions } from '../../../data/comm';
import { Table } from '../../../ui/Table';

const useStyles = makeStyles(theme => ({}), { name: 'MaterialCumspForm' });

export const MaterialCumspForm = () => {
  const classes = useStyles();

  const headers = [
    { label: '', width: 250 },
    { label: '', width: 200 },
    { label: '', width: 150 },
    { label: '', width: 150 },
    { label: '', width: 100 },
    { label: '' },
  ];
  const body = [
    { key: '', spec: '', unit: 'KG' },
    { key: '', spec: '', unit: 'KG' },
    { key: '', spec: '', unit: 'KG' },
    { key: '', spec: '', unit: 'KG' },
    { key: '_1', spec: '', unit: 'KG' },
    { key: '_2', spec: '', unit: 'KG' },
    { key: '_3', spec: '', unit: 'KG' },
    { key: '', spec: '', unit: 'PC' },
    { key: '', spec: '', unit: 'PC' },
    { key: '', spec: '', unit: 'PC' },
    { key: '', spec: '', unit: 'PC' },
    { key: '', spec: '', unit: 'KG' },
    { key: '', spec: '', unit: 'KG' },
    { key: '', spec: '', unit: 'PC' },
    { key: '()', spec: '', unit: 'PC' },
    { key: '()', spec: '', unit: 'PC' },
    { key: '()', spec: '', unit: 'PC' },
    { key: '()', spec: '', unit: 'PC' },
    { key: '()', spec: '', unit: 'PC' },
    { key: '()', spec: '', unit: 'PC' },
    { key: '()', spec: '', unit: 'PC' },
    { key: '()', spec: '', unit: 'PC' },
  ].map(d => createBodyData(d));

  return (
    <SectionWrapper icon={<SettingsApplications color="primary" fontSize="large" />} title="">
      <Grid container justify="space-between" alignItems="center" spacing={3}>
        <Table headers={headers} body={body} />
      </Grid>
    </SectionWrapper>
  );
};

function createBodyData({ key = '', spec = '', unit = '' }) {
  return [
    key,
    <SelectField dense options={venderOptions} />,
    spec,
    <MultiTextField dense textCenter fields={[{ placeholder: 'NNNNN' }]} />,
    unit,
    <MultiTextField dense textCenter fields={[{ placeholder: '' }]} />,
  ];
}
